.card {
    background: #fff;
    border-radius: 2px;
  
    height: 160px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    display: inline-block;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:black;
  }
  .card > .img-container > img {
    height: 100%;
  }
  

